 #cookie-banner {
     position: fixed;
     bottom: rem-calc(-500px);
     right: rem-calc(-500px);
     margin: rem-calc(56);
     width: 100%;
     max-width: rem-calc(280);
     background-color: $bodyBackground;
     padding: rem-calc(56);
     z-index: 9997;
     display: flex;
     flex-direction: column;
     justify-content: center;
     align-items: center;
     border-radius: rem-calc(21);
     box-shadow: rem-calc(0 0 21 0) rgba(#000, 0.5),
         inset rem-calc(0 0 0 3) rgba($bodyText, 0.07);

     &.active {
         bottom: 0;
         right: 0;
     }

     p {
         font-size: rem-calc(14);
         margin-bottom: rem-calc(21);
     }

     a {
         color: #007bff;
         text-decoration: none;

         &:hover {
             text-decoration: underline;
         }
     }
 }

 #accept-cookies {
     width: 100%;
     text-align: center;
     background-color: lighten($bodyBackground, 3%);
     color: $bodyText;
     padding: rem-calc(14 21);
     border: none;
     border-radius: rem-calc(7);
     cursor: pointer;
     transition: all 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);

     &:hover {
         background-color: lighten($bodyBackground, 7%);
     }
 }

// cokiebot
 @media screen and (max-width:720px) {
    #cookiebanner a.c-button { 
      width:100%; 
      box-sizing:border-box; 
      text-align:center; 
      margin-bottom:20px;} 
    #c-right { 
      float:none;
    }
    #c-left { 
      float:none;
    }   
    #cookiebanner p.c-message { 
      margin-bottom:20px;
    }   
  }    
  
  @media screen and (min-width:720px) {
    #cookiebanner #c-left { 
      float:left; 
      max-width:80%;
    }
    #cookiebanner #c-right { 
      float:right; 
    }
  }
  
  #cookiebanner { 
    box-sizing: border-box; 
    background-color:rgb(36,36,39); 
    opacity: 0.9; width:100%; 
    padding:25px 40px; 
    position: fixed; 
    z-index: 2147483645; 
    bottom:-800px
  }
  #cookiebanner p { 
    font-size:14px; 
    line-height:1.4;
  }
  #cookiebanner .c-header { 
    text-transform:uppercase; 
    color:#fff; 
    font-weight:bold; 
    margin-bottom:4px;
  }
  #cookiebanner p.c-message {
    font-size:14px; 
    color:#999;
  }
  #cookiebanner a { 
    font-weight:bold; 
    color:#999;
  }
  #cookiebanner a:hover { 
    color:#fff;
  }
  
  #cookiebanner a.c-button { 
    border-radius: 4px;
    background-color: rgb(255,255,255);
    box-shadow: 0 2px 0 0 rgb(221,221,221);
    border:1px solid rgb(221,221,221);
    padding:12px 60px;
    text-decoration:none;
    display:inline-block;
    vertical-align:middle;
    margin-top:10px;
    color:#666;
    font-size:16px;
    -o-transition:.2s;
    -ms-transition:.2s;
    -moz-transition:.2s;
    -webkit-transition:.2s;
    transition:.2s;
  }
  
  #cookiebanner a.c-button:hover { 
    box-shadow:none; 
    background-color:#f8f8f8;
  }