
// Animation for background Noise

@keyframes noise {
	0% {
		background-position: 0% 0%;
	}
	100% {
		background-position: 100% 100%
	}
}

//@keyframes hoverMe {
//	0%, 20%, 30%, 100%{ transform: scale(1);	}
//	10%, 20% { transform: scale(1.2);	}
//}

@keyframes hoverMe {
	 0%, 20%, 100% {transform: translate(-50%,-50%) scale(0.8);}
	 5%, 15% {transform: translate(-50%,-50%) scale(1.2);}
	10% {transform: translate(-50%,-50%) scale(1.0);}
}

@keyframes progressBarAnimation {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

@keyframes kustom-fade-in {
	0% {
		opacity: 0;
    transform: translate(calc(-50% - 84px), calc(-50% - 84px));
	}
	100% {
		opacity: 1;
    transform: translate(calc(-50% - 42px), calc(-50% - 42px));
	}
}

@keyframes kustom-fade-out {
	0% {
		transform: translate(calc(-50% + 42px), calc(-50% + 42px));
		opacity: 1;
	}
  60% {
    opacity: 0;
  }
	100% {
		transform: translate(calc(-50% + 84px), calc(-50% + 84px));
		opacity: 0;
	}
}
